      RU: "–ö—É—Ä–æ—Ä—Ç –Ø—Ä–µ–º—á–µ ‚Äì –æ–¥–∏–Ω –∏–∑ –Ω–∞–∏–±–æ–ª–µ–µ –∏–∑–≤–µ—Å—Ç–Ω—ã—Ö –∫–ª–∏–º–∞—Ç–∏—á–µ—Å–∫–∏—Ö –∫—É—Ä–æ—Ä—Ç–æ–≤ –ö–∞—Ä–ø–∞—Ç, —Å–∞–º—ã–π –ø—Ä–µ–∫—Ä–∞—Å–Ω—ã–π —É–≥–æ–ª–æ–∫ —ç—Ç–∏—Ö –≥–æ—Ä.       –†–∞—Å–ø–æ–ª–æ–∂–µ–Ω–Ω—ã–π –≤ –∂–∏–≤–æ–ø–∏—Å–Ω–æ–π –¥–æ–ª–∏–Ω–µ —Ä–µ–∫–∏ –ü—Ä—É—Ç, –≥–æ—Ä–æ–¥ –æ—á–∞—Ä–æ–≤—ã–≤–∞–µ—Ç —Å–≤–æ–µ–π –∫—Ä–∞—Å–æ—Ç–æ–π. –Ø—Ä–µ–º—á–µ —Å–ª–∞–≤–∏—Ç—Å—è —Å–≤–æ–∏–º–∏ –ø–∞–º—è—Ç–Ω–∏–∫–∞–º–∏ –∞—Ä—Ö–∏—Ç–µ–∫—Ç—É—Ä—ã.       –ù–æ —Å–∞–º–∞—è –∏–∑–≤–µ—Å—Ç–Ω–∞—è –¥–æ—Å—Ç–æ–ø—Ä–∏–º–µ—á–∞—Ç–µ–ª—å–Ω–æ—Å—Ç—å ‚Äì –≤–æ–¥–æ–ø–∞–¥ '–ì—É–∫' –≤ –º–µ—Å—Ç–Ω–æ–º –ø–∞—Ä–∫–µ.       –ö—É—Ä–æ—Ä—Ç —Å–ª–∞–≤–∏—Ç—Å—è –Ω–µ —Ç–æ–ª—å–∫–æ –∂–∏–≤–æ–ø–∏—Å–Ω—ã–º–∏ –ª–∞–Ω–¥—à–∞—Ñ—Ç–∞–º–∏, –Ω–æ –∏ —Ä–∞–∑–≤–∏—Ç–æ–π –∏–Ω—Ñ—Ä–∞—Å—Ç—Ä—É–∫—Ç—É—Ä–æ–π, –≥—É—Ü—É–ª—å—Å–∫–∏–º –∫–æ–ª–æ—Ä–∏—Ç–æ–º –∏ —Å–æ–≤—Ä–µ–º–µ–Ω–Ω—ã–º–∏ –≥–æ—Å—Ç–∏–Ω–∏—á–Ω—ã–º–∏ –∫–æ–º–ø–ª–µ–∫—Å–∞–º–∏",
      EN: "Yaremche resort is one of the most famous climatic resorts in the Carpathians, the most beautiful corner of these mountains. Located in the picturesque valley of the Prut River, the city enchants with its beauty. Yaremche is famous for its architectural monuments. But the most famous attraction is the 'Guk' waterfall in the local park. The resort is famous not only for its picturesque landscapes, but also for its developed infrastructure, Hutsul flavor and modern hotel complexes.",
